@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root {
  --sidebar-flex:1.8;
  --condiv-flex:6;
  --fontsize-p:1.0rem;
  --fontsize-p-bigger:1.2rem;
  --fontsize-nav:1.4rem;
  --typing-font:2rem;
  --padding-mobile:5px;
}
* {
  margin:0;
  padding: 0;
}
body {
  font-family: 'Raleway';
  background: rgba(125,103,185,1);
}
.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
  background: rgba(125,103,185,1);
}
nav {
  max-width: 220px;
  min-width: 180px;
  width: !important 180px;
  min-height: 100vh;
  padding:0px 20px;
  /* flex:var(--sidebar-flex); */
  background: rgb(238,103,143);
  /* background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%); */
  background: linear-gradient(180deg, rgba(125,103,185,1) 50%, rgba(132,124,252,1) 80%);
}
.condiv {
  padding: 65px;
  padding-top: 40px;
  background-color: #f4f5f9;
  flex:var(--condiv-flex);
  max-width: 1000px;
}
.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.profile_img {
  border-radius: 50%;
  width: 150px;
  margin: 30px;
}
.typingeffect {
  font-size: var(--typing-font);
  margin-bottom: 30px;
}


/* Contains all profile detail in the nav */
.profile_box {
  /* background: #fff; */
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 74vh;
  position: relative;
  margin: 0 -2px;
}

.profile_bg {
  background: #666;
  width: 185px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  margin-bottom: 8px;
  border: 0.5px solid rgba(250,250,250, 0.2);
}

/* Border box around profile image*/
.profile_photo_border {
    position: absolute;
    top: -20px;
}
 /* Profile image container */
.profile_img_box {

}

.profile_bannerol_name {
  font-size: 24px;
  font-family: cursive;
}

.profile_bannerol {
  padding: 0px 2rem;
  margin-left: 0px;
  margin-right: -10px;
  position: absolute;
  top: 150px;

  color: #fefeff;
  height: 28px;
  width: 150%;
  line-height: 6px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background-image:
                    linear-gradient(-45deg, transparent 20%, black 20%, black 80%, transparent 80%),
                    linear-gradient(45deg, transparent 20%, black 20%, black 80%, transparent 80%);
  background-origin: border-box;
  box-sizing: border-box;
  border-left: 0rem transparent solid;
  border-right: 0rem transparent solid;
}

.profile_bannerol::after {
  display: block;
  height: 2px;
  width: 170px;
  /* background: #808080; */
  border-bottom: 1px solid rgba(250,250,250, 0.3);
  content: "";
  margin: 0 auto;

}


.profile_desc_box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 190px;
  text-align: center;
  color: rgba(250,250,250, 0.8);
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  width: 185px;
}

/* Title for the lists of topics of interest in profile */
.profile_title_desc_text {
  padding-bottom: 10px;
}
.profile_end_desc_text {
  margin-top: -10px;
}
.profile_end_desc_text::after {
  display: block;
  width: 125px;
  border: 0.5px solid rgba(205, 250, 250, 0.3);
  margin-top: 20px;
  content: "";
  min-width: 0px;
}
.profile_end_desc_text::after {
  min-width: 0px;
}

nav ul{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
  padding: 10px 0;
}
nav ul li {
  margin:5px;
}
a {
  text-decoration: none;
  color: white;
}
.subtopic {
  margin:10px 10px 30px 10px !important;
}
p {
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,h5,h6,p {
  margin: 10px;
}
.social {
  position: absolute;
  display: flex;
  bottom: 20px;
  margin: 0 auto;
}
.social i {
  padding: 0 10px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover {
  color: #ffffff;
}
.widecard {
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active {
  font-weight: bolder;
}
.skills ul li {
  list-style-type:none;
  margin:30px;
}

.secondtext_grey {
  color: grey;
}

/* Scroll button */
.scroll {
  visibility: hidden;
}






/*********************
  Change Screen Size
**********************/

@media all and (max-width: 788px) {
  .condiv {
    padding: 20px;
  }
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
    --fontsize-p:1.0rem;
    --typing-font:1rem;
  }
  h1 {font-size: 1.6rem;}
  h3 {font-size: 1.15rem;}
  h4 {font-size: 0.85rem;}
  h6 {font-size: 0.7rem;}
  .App {
    flex-direction: column;
  }
  .sidebar {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(200,204,252,1) 50%, rgba(132,124,252,1) 80%);
  }
  nav {
    max-width: 100vw;
    width: 100vw;
    min-height: 100vh;
    padding:0;
    margin-right: 1px;
    position: relative;
  }
  nav ul {
    display: flex;
    left:0;
    right: 0px;
    width: inherit;
    display: flex;
    /* justify-content: space-around; */
    justify-content: center;
    bottom: 30px;
    margin: 0 auto;
  }
  nav ul li {
    margin: 10px;
  }
  .profile_box {
    min-height: 405px;
    margin-right: -1px;
    height: 40vh;
  }
  .profile_bg {
    min-height: 425px;
    width: 60vw;
    min-width: 255px;
    background: none;
  }
  .profile_bannerol {
    padding: 0px 2rem;
    margin-left: 0px;
    margin-right: -10px;
    top: 150px;
    height: 28px;
    width: inherit;
  }
  .profile_desc_box {
    width: 60vw;
  }
  .profile_desc_text {
    width: 95%;
  }
  .profile_end_desc_text::after {
    visibility: hidden;
  }
  .profile_desc_box > h6 {
    font-size: 1.1rem;
  }
  .social {
    left:0;
    right: 0px;
    width: inherit;
    display: flex;
    /* justify-content: space-around; */
    justify-content: center;
    bottom: 10px;
    margin: 0 auto;
  }
  .social i {
    padding-right: 5px;
    padding-left: 5px;
    color: #fafbfc;
    opacity: 0.6;
      &:hover {
        opacity: 1;
      }
  }

  /* Content Box */
  .condiv {
    /* width: 80vw; */
    /* justify-content: center; */
    min-width: 180px;
    padding:0px;
    padding-top: 20px;
    padding-bottom: 50px;
    padding-left: var(--padding-mobile);
    /* padding-left: 10px;; */
    position: relative;
    /* Correct that the NAV takes full screen */
    top: -400px;
    margin-bottom: -400px;
  }
  /* Scroll button */
  .scroll {
    visibility: visible;
    opacity: 0.5;
    height: 25px;
    /* position: fixed; */
    position: relative;
    bottom: -25px;
    width: 70px;
    border-radius: 10px;
    border: none;
    background: rgba(125,93,195,1);
    padding-top: 5px;
    padding-bottom: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    left: -1%;
      &:hover {
        opacity: 1;
        background: rgba(125,103,185,1);
        border-color: none;
      }
  }
  .arrow-up {
    color: rgba(250,250,250,0.8);
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -9px;
    margin-left: -19px;
    margin-right: 10px;
    &:hover {
      color: white;
    }
  }
  .scroll_mobile {
    padding-top: 25px;
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: var(--padding-mobile);
  }
  .scroll_mobile a {
    color: rgba(150,150,150,0.8);
  }
  .scroll_mobile a:visited {
    color: rgba(150,150,150,0.8);
  }
  .scroll_mobile a:hover {
    color: red;
  }
}


@media all and (max-width: 275px) {
  html {min-width: 240px;}
  #App {min-width: 240px;}
  .sidebar {min-width: 240px;}
  nav {
    min-width: 245px
  }
  nav ul li {
    margin: 10px 5px;
  }
  .profile_desc_text {
    width: 75%;
  }
  .condiv {min-width: 240px;}
}

